// models
import { OrderStatusType } from 'models';

// FUNCTION
export const getStatusText = (status: OrderStatusType): string => {
  // Define the text of the status
  let text = '';
  switch (status) {
    case 'created':
      text = '';
      break;
    case 'pending':
      text = '';
      break;
    default:
      text = '';
  }

  return text;
};
